// name: eg. 'tier1'
// key:  eg. 'base'
@function get-color($name, $key) {
	$map: map-get($colors-map, $name);
	$val: map-get($map, $key);
	@return $val;
}
*/
/* https://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: 'anybodymedium';
  src: url("../../../../assets/fonts/anybody/Anybody-Medium.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

:root {
  color-scheme: dark;
}

html {
  font-size: 16px;
}

body {
  background: linear-gradient(180deg, #585858 0%, #272727 100%);
  color: #aaa;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.8;
}

body * {
  box-sizing: border-box;
}

h1 {
  color: #fff;
  font-size: 1.8rem;
}

h2 {
  font-size: 1.4rem;
  margin-bottom: 20px;
  text-align: center;
}

a {
  color: #fff;
}

em {
  font-style: italic;
}

strong {
  font-weight: 600;
}

code:not(.overlay),
pre:not(.overlay) {
  background-color: rgba(0, 0, 0, 0.8);
  color: rgba(255, 255, 255, 0.8);
  font-family: consolas;
  padding: 1px;
}

pre:not(.overlay) {
  white-space: pre-wrap;
}

label {
  cursor: pointer;
}

/**
 * Buttons
 */
.btn {
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.6);
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  padding: 5px 10px;
}

.btn--default-bg {
  background: rgba(255, 255, 255, 0.1);
}

.btn--default-bg:hover {
  background: rgba(255, 255, 255, 0.3);
}

.btn--large {
  font-weight: 600;
  padding: 10px 15px;
}

.btn--has-bg:hover {
  opacity: 0.4;
}

.btn--has-bg.btn--active {
  opacity: 0.6;
}

.btn--red {
  background-color: #791a1a;
  color: #fff;
}

.btn--red:hover {
  background-color: #693c3c;
}

.btn--staticon {
  margin-bottom: 3px;
  padding: 4px 5px;
}

.btn--staticon img {
  height: auto;
  vertical-align: middle;
  width: 18px;
}

.btn--savetoggle {
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
}

.btn--submit {
  background-color: #1f791a;
  color: #fff;
}

.btn--submit:hover {
  background-color: #3c6943;
}

.tierbtns {
  display: flex;
  justify-content: flex-end;
}

.btn--tier {
  border-radius: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
  height: 30px;
  width: 30px;
}

.btn--tier .btn__text {
  font-size: 1rem;
  line-height: 100%;
  text-align: center;
  position: relative;
  top: 1px;
}

.icon-btns {
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  justify-content: center;
}

.icon-btn {
  cursor: pointer;
  margin-bottom: 5px;
  margin-right: 3px;
}

.icon-btn:hover {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 3px;
}

.icon-btn--active {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 3px;
}

.icon-btn img {
  display: block;
  width: 40px;
}

/**
 * Structure
 */
.wrap {
  align-items: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  display: flex;
  width: 100%;
}

.header {
  margin-top: 20px;
  padding: 5px 10px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.header h1 {
  font-size: 1.6rem;
  color: #fff;
  text-align: center;
  line-height: 40px;
  height: 40px;
}

.header .header__save {
  display: none;
}

/**
 * Panels
 */
.panels {
  align-items: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 10px;
}

.panel {
  background: #161616;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  margin: 0 10px;
  min-height: 400px;
  padding: 15px;
  position: sticky;
  top: 20px;
}

.panel--saved {
  width: 15%;
  max-width: 250px;
}

.panel--editor {
  width: 50%;
  max-width: 720px;
  padding: 25px 30px;
}

.panel--result {
  width: 30%;
  max-width: 425px;
}

.ibox {
  border-radius: 4px;
  color: #fff;
  font-size: 15px;
  max-width: 400px;
  min-width: 200px;
  opacity: 1;
  padding: 10px;
  transition: 0.1s;
  width: 300px;
  border: 1px solid;
}

.ibox__top {
  display: flex;
  margin-bottom: 10px;
}

.ibox__icon {
  background-color: rgba(255, 255, 255, 0.1);
  flex-shrink: 0;
  height: 60px;
  margin-right: 10px;
  width: 60px;
}

.ibox__icon img {
  height: 60px;
  opacity: 0.2;
  width: 60px;
}

.ibox__name {
  font-size: 1.1em;
  line-height: 1.2;
  word-break: break-word;
  transition: 0.1s;
}

.ibox__top-right {
  width: calc(100% - 70px);
}

.ibox__type {
  color: #eae2b0;
  font-size: 1.1em;
}

.ibox__stats {
  font-size: 1em;
  line-height: 1.5;
  white-space: pre-wrap;
}

.staticon {
  display: inline-block;
}

.staticon img {
  height: auto;
  vertical-align: middle;
  width: 20px;
}

/**
 * Create (main editor)
 */
.row {
  margin-bottom: 10px;
}

.row--buttons {
  margin-top: -5px;
}

.labelwrap {
  display: flex;
}

.labelwrap--has-btns .field--buttons {
  margin-left: 15px;
}

.labelwrap--nocols {
  display: block;
}

.labelwrap--nocols .labeltext {
  width: 100%;
}

.labeltext {
  font-weight: 600;
  padding: 5px 10px 10px 10px;
  width: 100px;
}

.labeltext--empty {
  padding: 0;
}

.field select,
.field input[type="text"],
.field input[type="number"],
.field textarea {
  background: #000;
  border-radius: 3px;
  border: 1px solid grey;
  color: #fff;
  font-size: 1rem;
  padding: 10px;
  width: 200px;
}

.field textarea {
  font-size: 0.9rem;
}

.field input[type="checkbox"] {
  padding: 10px;
  width: 20px;
}

.field--tier select {
  width: 88px;
}

.field--width input[type="number"] {
  width: 100px;
}

.field--has-checkbox {
  padding: 10px 0;
}

.field--editor textarea {
  min-height: 150px;
  max-height: 300px;
  min-width: 300px;
  max-width: 500px;
  width: 100%;
}

.field--buttons button {
  margin-right: 5px;
}

.helpnotes {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.helpnote {
  color: rgba(255, 255, 255, 0.4);
  padding: 0 10px;
  text-align: center;
}

.ibox--editable {
  max-width: unset;
  width: 100%;
  border-width: 2px;
  border-radius: 5px;
}

.ibox--editable input, .ibox--editable textarea {
  background: rgba(255, 255, 255, 0.1);
  border: 1px dashed rgba(255, 255, 255, 0.2);
  font-size: inherit;
  color: inherit;
  width: 100%;
  font-family: inherit;
  font-weight: inherit;
}

.ibox--editable .ibox__icon {
  cursor: pointer;
}

.ibox--editable .ibox__name {
  margin-bottom: 3px;
}

.ibox--editable .ibox__name input, .ibox--editable .ibox__name textarea {
  font-size: 1.32em;
}

.ibox--editable .ibox__type input {
  font-size: 1.21em;
}

.ibox--editable .ibox__stats {
  position: relative;
  line-height: 1.8;
  font-size: 1rem;
  letter-spacing: 0.7px;
}

.ibox--editable .ibox__stats textarea {
  height: 250px;
  max-width: 100%;
  min-width: 100%;
  max-height: 400px;
  min-height: 200px;
  line-height: inherit;
  font-size: inherit;
  letter-spacing: inherit;
}

.ibox--editable .ibox__stats .ibox__stats-overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.7;
  padding: 10px;
  pointer-events: none;
  user-select: none;
}

.editortext {
  margin-top: 20px;
}

.editortext__row {
  margin-bottom: 10px;
}

.editortext__row--smb {
  margin-bottom: 5px;
}

.width-opts {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: -10px;
}

.width-opts .btn {
  border-radius: 4px 0 0 4px;
}

.width-opts .btn + .btn {
  border-radius: 0 4px 4px 0;
  border-left-width: 0;
}

/**
 * Result
 */
.result {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

#canvas {
  border: 1px dashed #343434;
}

/**
 * Saved Items
 */
.saved-item {
  display: flex;
  font-size: 0.9rem;
  justify-content: space-between;
  line-height: 1.5;
  margin-bottom: 5px;
}

.saved-item__main {
  width: 90%;
}

.saved-item__main:hover {
  background: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}

.saved-item__img {
  margin-right: 5px;
}

.saved-item__img img {
  height: auto;
  width: 16px;
  vertical-align: middle;
}

.saved-item__number {
  color: rgba(255, 255, 255, 0.1);
  margin-right: 5px;
}

.saved-item__name {
  color: #fff;
  word-break: break-word;
}

.saved-item__delete {
  background-color: #791a1a;
  border-radius: 3px;
  height: 20px;
  margin-left: 10px;
  text-align: center;
  width: 10%;
}

.saved-item__delete:hover {
  background-color: #693c3c;
  cursor: pointer;
}

footer {
  margin-top: 10px;
  text-align: center;
}

footer .tips {
  font-size: 0.9rem;
}

footer .credit {
  font-size: 0.7rem;
  margin-top: 20px;
}

