/**
 * Utility
 */
.text-upper {
  text-transform: uppercase;
}

.text-positive {
  color: lime !important;
}

.text-positive--pastel {
  color: #66b966 !important;
}

.text-negative {
  color: #f40000 !important;
}

.text-negative--pastel {
  color: #bf5959 !important;
}

.text-cream {
  color: #eae2b0 !important;
}

.text-grey {
  color: #b2b2b2 !important;
}

.mt-5 {
  margin-top: 5px;
}

.flex {
  display: flex;
}

.flex-center {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.flex-right {
  align-items: flex-end;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.rounded {
  border-radius: 4px;
}

.tip {
  font-size: 0.7rem;
}
